          <div class="column is-6 p-5">
            <div class="flex-block-horizontal is-flex mb-3">
              <div class="circle list-circle has-background-student">1</div>
              <div class="flex-block">
                <h3 class="is-size-2 mb-1">Class Presentations</h3>
                <div class="has-text-grey is-size-5">In-person, live/virtual, or recorded</div>
              </div>
            </div>
            <p class="is-size-4">A low-intensity activity that is primarily focused on student career exploration. Industry professionals will be invited to provide a broad exposure to their industry and occupations. This also includes the #EveryoneHasAStory&trade; recorded career story videos.</p>
          </div>
          <div class="column is-6 p-5">
            <div class="flex-block-horizontal is-flex mb-3">
              <div class="circle list-circle has-background-student">
