              </div>
            </div>
            <p class="is-size-4">A low-intensity activity that is primarily focused on student career exploration. Site visit/field trip hosts will be invited to provide a facility tour along with a broad exposure to the subject industry and related occupations to a classroom-sized group of students with a teacher chaperone.</p>
          </div>
