                <div class="has-text-grey is-size-5">In-person, live/virtual, or recorded</div>
              </div>
            </div>
            <p class="is-size-4">Speakers bureau volunteers may be asked to participate individually or as a member of an industry or career panel, to share career or economic information and/or subject matter expertise with a group of students, parents and/or staff. This is a low-intensity activity.</p>
          </div>
          <div class="column is-6 p-5">
            <div class="flex-block-horizontal is-flex mb-3">
              <div class="circle list-circle has-background-student">4</div>
              <div>
                <h3 class="is-size-2 mb-1">Mentorships</h3>
                <div class="has-text-grey is-size-5">In-person or remote learning/training</div>
              </div>
            </div>
            <p class="is-size-4">A mentorship is a medium intensity work-based learning activity that is primarily focused on project-based learning. Professionals from the community share their subject matter expertise with a classroom of students, a small group of students, or individual students involved in experiential learning or capstone projects.</p>
          </div>
        </div>
        <div class="columns">
          <div class="column is-6 p-5">
