              </div>
            </div>
            <p class="is-size-4">A job shadow &amp; informational interview is a medium-intensity activity that is primarily focused on student occupational investigation. These experiences are one-one with an industry professional that allow a student to interview and observe an industry professional in a work setting. This also includes the #EveryoneHasAStory&trade; recorded career story videos.</p>
          </div>
