                <div class="has-text-grey is-size-5">In-person, live/virtual, or recorded</div>
              </div>
            </div>
            <p class="is-size-4">An internship is a higher intensity activity. It is a structured work-based learning experience of a duration of 60 or 120 hours that meets requirements for elective course credit for a student. Generally reserved for students ages 17-18 but work rules could allow internships to age 14</p>
          </div>
        </div>
        <div class="columns">
          <div class="column is-6 p-5">
            <div class="flex-block-horizontal is-flex mb-3">
              <div class="circle list-circle has-background-student">7</div>
