                <div class="has-text-grey is-size-5">In-person, live/virtual, or recorded</div>
              </div>
            </div>
            <p class="is-size-4">These opportunities are provided by employers, educational institutions, training institutions or community organizations. Opportunities may include: educational camps, training programs, certificate programs, volunteering, apprenticeships or job postings for students.</p>
