                <div class="has-text-grey is-size-5">In-person or remote learning/training</div>
              </div>
            </div>
            <p class="is-size-4">An Industry Sponsored Project is a medium-intensity activity where students complete a real-world project or problem submitted and monitored by an industry professional. Students are mentored by their teacher to ensure the project meets set expectations.</p>
          </div>
