    <section class="section has-padding-large has-background-grey-default">
      <div class="container has-text-centered">
        <h2 class="section-h2">Why Should I partner?</h2>
        <img class="graph-img mb-5" src="@/assets/img/boy-graph.svg" loading="lazy" alt="Boy using a graph as a stairway">
        <div class="tp-content content">
          <p class="is-size-4 _51ch">Our business representatives partner for a variety of reasons. Many are taking action to solve chronic talent supply shortages. Others are working with us to help close skills gaps for students entering the workforce. Still others just want to give back to the community by being part of a knowledge transfer process to the next generation.</p>
          <p class="is-size-4 _51ch">Whatever your reason to take the next step might be, please know that PPBEA has created systems and practices that make it simple and easy to get involved. You don’t have to be an education expert. We will walk you through the entire process!</p>
          <p class="is-size-4 _51ch">In many occupations, the days of posting positions and hoping qualified candidates show up are over. Depending on the research, anywhere from 65% to 83% of business owners and hiring managers report having difficulty filling open positions.</p>
          <p class="is-size-4 _51ch">Hiring is most difficult in a select group of high demand occupations throughout the U.S. There are strategies that businesses can adopt to help solve these problems. One of these key strategies is to partner with K-12 education to "grow our own". Here we are!</p>
        </div>
      </div>
    </section>

    <section class="section">
